body {
    z-index: 1;
    margin: 0;
    padding: 0;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif;
    background: linear-gradient(120.39deg, #D29C93 11.61%, #BA84A2 114.96%), #7E1AFC no-repeat;
}

