.searchResults {
    position: absolute;
    width: 248px;
    height: auto;
    box-sizing: border-box;
    background-color: rgba(231, 231, 231, 0.986);
    top: 106px;
    left: 783px;
    border-radius: 5px;
    display: none;
    color: black;
    z-index: 5;
    font-family: roboto;
    opacity: 0.9;
    
}


.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    display: none;
    background-color: rgba(5, 10, 30, 0.1);
    left: 0;
    top: 0;
}

body.overlayVisible .overlay,body.results-visible .searchResults {
    display: block;
}


.seriesSearch {
    position: absolute;
    top: 50px;
    left: 783px;
    width: 248px;
    height: 45px;
    border: 1px solid white;
    background: #FFFFFF;
    border-radius: 3px; 
    background-image: url(./iconmonstr-search-thin.svg) ;
    background-repeat: no-repeat;
    background-position: 10px;
    padding-left: 65px;
    box-sizing: border-box;
    display: inline-block;
    
    
}

input::placeholder {
    color:  #AF6676;
    font-size: 15px;
    font-family: roboto;
}

.logo{
    width: 130px;
    height: 35px;
    position: absolute;
    left: 335px;
    top: 55px;
}

.container {
    box-sizing: border-box;
    display: inline-block;
    background: #FFFFFF;
    box-shadow: 0px 4px 47px rgba(0, 0, 0, 0.26);
    border-radius: 10px;
    width: 700px;
    height: auto;
    margin: 106px 0 0 331px;
    box-shadow: 0px 4px 47px rgba(0, 0, 0, 0.26);
    z-index: 1;
}

.ironThrone {
    margin: 25px 0 0 17px;
    border-radius: 3px;
    float: left;
    
}

.infoContainer {
    width: 400px;
    display: inline-block;
    box-sizing: border-box;
    height: auto;
    margin: 25px;
}

h1 {
    margin: 0 0 5px 0;
    width: 290px;
    font-weight: 500;
    font-size: 30px;
    line-height: 29px;
    font-family: 'Montserrat', sans-serif;
    box-sizing: border-box;
    display: inline-block;
}

.broadcast {
    color: #AF6676;
    font-weight: 500;
    box-sizing: border-box;
    margin-left: 4px;
    box-sizing: border-box;
    display: inline-block;
}

.vote {
    background: url(./star.svg) no-repeat 7px;
    width: 65px;
    height: 30px;
    float: right;
    border: 1px solid rgba(240, 162, 179, 0.7);
    border-radius: 4px;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}



.rating {
    float: right;
    font-weight: 600;
    font-size: 20px;
}

.type {
    background: #DDE0EC;
    border-radius: 3px;
    margin: 24px 0 0 5px;
    color: rgba(0, 0, 0, 0.7);
    width: 85px;
    height: 28px;
    font-size: 11px;
    font-weight: 400;
    border: 1px solid #DDE0EC;
}

.summary {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #665F5F;
    margin-top: 20px;
}

a {
    color: black;
    text-decoration: none;
    padding: 5px 0 5px 10px;
    box-sizing: border-box; 
    display: inline-block;
    font-family:  -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
}

li {
   list-style-type: none; 
}


